
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import {detail} from '../config'
export default {
  head: {
    title: {
      inner: ''
    }
  },
  components: {
    Scroller,
    Group,
    Cell
  },
  data () {
    return {
      balance: 0,
      form: {
        userId: '',
        type: 1,
        limit: 10,
        pageIndex: 0
      },
      list: []
    }
  },
  mounted () {
    this.balance = this.$localStorage.get('balance')
    this.form.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
    this.getList()
  },
  methods: {
    getList () {
      this.$http({
        method: 'jsonp',
        url: detail,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: this.form
      })
      .then(res => {
        console.log(res)
        this.list = res.body.data.scoreList
        for (const i in this.list) {
          this.list[i].createTime = dateFormat(this.list[i].createTime)
        }
      })
    }
  }
}



